import { mapGetters } from 'vuex';
import { UploadPicker, getUploader } from '@nextcloud/upload';
import { NcEmptyContent } from '@nextcloud/vue';
import VirtualGrid from 'vue-virtual-grid';
import FileLegacy from '../components/FileLegacy.vue';
import Folder from '../components/Folder.vue';
import HeaderNavigation from '../components/HeaderNavigation.vue';
import { prefixPath } from '../services/DavClient.js';
import allowedMimes from '../services/AllowedMimes.js';
import getAlbumContent from '../services/AlbumContent.js';
import AbortControllerMixin from '../mixins/AbortControllerMixin.js';
import GridConfigMixin from '../mixins/GridConfig.js';
import getFileInfo from '../services/FileInfo.js';
export default {
  name: 'Folders',
  components: {
    VirtualGrid,
    HeaderNavigation,
    NcEmptyContent,
    UploadPicker
  },
  mixins: [AbortControllerMixin, GridConfigMixin],
  props: {
    rootTitle: {
      type: String,
      required: true
    },
    path: {
      type: String,
      default: '/'
    },
    showShared: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      error: null,
      allowedMimes,
      initializing: true,
      loading: false,
      appContent: document.getElementById('app-content-vue'),
      uploader: getUploader()
    };
  },
  computed: {
    // global lists
    ...mapGetters(['files', 'folders']),
    // current folder id from current path
    folderId() {
      return this.$store.getters.folderId(this.path);
    },
    // files list of the current folder
    folder() {
      return this.files[this.folderId];
    },
    folderContent() {
      return this.folders[this.folderId];
    },
    fileList() {
      const list = this.folderContent && this.folderContent.map(id => this.files[id]).filter(file => !!file);
      return list;
    },
    // subfolders of the current folder
    subFolders() {
      return this.folderId && this.files[this.folderId] && this.files[this.folderId].folders;
    },
    folderList() {
      const list = this.subFolders && this.subFolders.map(id => this.files[id]).filter(file => !!file);
      return list;
    },
    contentList() {
      const folders = this.folderList?.map(folder => {
        return {
          id: `folder-${folder.fileid}`,
          injected: {
            ...folder,
            showShared: this.showShared
          },
          width: 256,
          height: 256,
          columnSpan: 1,
          renderComponent: Folder
        };
      });
      const files = this.fileList?.map(file => {
        return {
          id: `file-${file.fileid}`,
          injected: {
            ...file,
            list: this.fileList
          },
          width: 256,
          height: 256,
          columnSpan: 1,
          renderComponent: FileLegacy
        };
      });
      return [...(folders || []), ...(files || [])];
    },
    // is current folder empty?
    isEmpty() {
      return !this.haveFiles && !this.haveFolders;
    },
    haveFiles() {
      return !!this.fileList && this.fileList.length !== 0;
    },
    haveFolders() {
      return !!this.folderList && this.folderList.length !== 0;
    }
  },
  watch: {
    path() {
      this.fetchFolderContent();
    },
    showShared() {
      this.fetchFolderContent();
    }
  },
  beforeMount() {
    this.fetchFolderContent();
  },
  methods: {
    onRefresh() {
      this.fetchFolderContent();
    },
    async fetchFolderContent() {
      this.error = null;
      this.loading = true;

      // close any potential opened viewer & sidebar
      OCA?.Viewer?.close?.();
      OCA?.Files?.Sidebar?.close?.();

      // if we don't already have some cached data let's show a loader
      if (!this.files[this.folderId] || !this.folders[this.folderId]) {
        this.initializing = true;
      }
      try {
        // get content and current folder info
        const {
          folder,
          folders,
          files
        } = await getAlbumContent(this.path, {
          shared: this.showShared,
          signal: this.abortController.signal
        });
        this.$store.dispatch('addPath', {
          path: this.path,
          fileid: folder.fileid
        });
        this.$store.dispatch('updateFolders', {
          fileid: folder.fileid,
          files,
          folders
        });
        this.$store.dispatch('updateFiles', {
          folder,
          files,
          folders
        });
      } catch (error) {
        if (error.response && error.response.status) {
          if (error.response.status === 404) {
            this.error = 404;
            setTimeout(() => {
              this.$router.push({
                name: this.$route.name
              });
            }, 3000);
          } else {
            this.error = error;
          }
        }
        // cancelled request, moving on...
        console.error('Error fetching album data', error);
      } finally {
        // done loading even with errors
        this.loading = false;
        this.initializing = false;
      }
    },
    /**
     * Fetch file Info and add them into the store
     *
     * @param {Upload[]} uploads the newly uploaded files
     */
    onUpload(uploads) {
      uploads.forEach(async upload => {
        const relPath = upload.path.split(prefixPath).pop();
        const file = await getFileInfo(relPath);
        this.$store.dispatch('appendFiles', [file]);
        this.$store.dispatch('addFilesToFolder', {
          fileid: this.folderId,
          files: [file]
        });
      });
    }
  }
};